.underline {
  position: relative; }
  .underline:hover, .underline:focus, .underline:active {
    cursor: pointer; }
    .underline:hover:after, .underline:focus:after, .underline:active:after {
      width: 100%; }
  .underline:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    height: 2px;
    background-color: #FF4141;
    width: 0;
    -webkit-transition: width .4s;
    -o-transition: width .4s;
    transition: width .4s; }

